import SecurityVerifyModal from '~/components/Account/SecurityVerifyModal'

export default {
  name: 'Frozen',
  head(){
    return{
      title: 'Hesap Dondurma | Cointral.com',
    }
  },
  data: () => ({
    isMobile: null,
    isModalVisible: false,
    isDisabled: false,
    errorMessage: null,
    isError: false,
    verifyData: {
      Token: null,
      errorCode: null,
      showDialog: false,
    },
  }),

  mounted() {
    this.isMobile = window.innerWidth < 1000

    window.addEventListener('resize', () => {
      this.isMobile = window.innerWidth < 1000
    })
  },

  methods: {
    verify() {
      
      this.isModalVisible = false
    },

    cancelVerify() {
      
      this.isModalVisible = false
    },

    remindMeLater() {
      
      this.isModalVisible = false
    },

    async frozeAccount() {
      const {
        $axios,
        $cookies,
        $config: { FINANCE_API_URL },
      } = this
      let authHeaders = { Authorization: 'Bearer ' + $cookies.get('Token') }
      const response = await $axios.post(
        FINANCE_API_URL + '/FrozeAccount',
        null,
        {
          headers: authHeaders,
        }
      )
      if (response.data.Success) {
        
        //this.isDisabled = true
        this.verifyData.Token = response.data.SecurityVerificationToken
        this.verifyData.errorCode = response.data.ErrorCode
        this.verifyData.showDialog = true
      } else {
        
        this.isModalVisible = true
        let messages = response.data.Message.split(',')
        if (messages.length === 1) {
          this.errorMessage = `Lütfen ${messages[0].toString()} tamamlayınız.`
          this.isError = true
        } else {
          this.errorMessage = `Lütfen ${messages[0].toString()} ve ${messages[1].toString()} tamamlayınız.`
          this.isError = true
        }
        setTimeout(() => (this.isError = false), 5000)
      }
    },
  },
}
